import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class SelectClass extends Component {
    constructor() {
        super()
        this.state = {
            classes: [
                {
                    name: "Senior Secondary",
                    id: 1
                },
                {
                    name: "Junior Secondary",
                    id: 2
                }
            ]
        };
    }
    preventBack = () => {
        window.history.pushState(null, null, window.location.href);
        window.onpopstate = function () {
            window.history.go(1)
        }
    }
    render() {
        const { classes } = this.state;
        return (
            <div>
                <h1 className="uk-text-center">INSTRUCTIONS</h1>
                <p className="uk-text-center">Read these instructions before starting the exam</p>

                <ul>
                    <li>Questions answered will be indicated by a green button <button className="uk-button" style={{ backgroundColor: "rgb(90, 209, 90)" }}>1</button> while unanswered questions will be indicated by a transparent button <button className="uk-button">1</button></li>
                    <li>Only submit once you have answered the questions you can or choose to answer because once you have submitted it cannot be reversed</li>
                    <li>Try not to <span style={{ color: "rgb(235, 68, 57)", fontWeight: "bold" }}>CHEAT</span> during this exam. it's for your own good</li>
                    <li>Do not refresh the page during the exam or else you will loose your progress</li>
                    <li>Upon submission your score will be shown you</li>
                    <li>Select your appropriate class below to begin <span style={{ color: "green", fontWeight: "bold" }}>GOOD LUCK</span></li>
                </ul>

                <div className="uk-flex uk-flex-center" data-uk-grid>
                    {
                        classes.map(klass => (
                            <Link to={`/cbt/${klass.name}`} key={klass.id} onClick={this.preventBack}><button className="uk-button uk-button-default uk-margin ">{klass.name}</button></Link>
                        ))
                    }

                </div>
            </div>

        );
    }
}

export default SelectClass;