import React from 'react';
import './App.css';
import Navigation from "./components/Navigation.jsx";
import Slideshow from './components/Slideshow.jsx';
import About from "./components/About.jsx";
import Footer from "./components/Footer.jsx";
import Testimony from './components/Testimonies';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "uikit/dist/css/uikit.min.css";
import "uikit/dist/js/uikit.min.js";
import "uikit/dist/js/uikit-icons.js";
import Questions from './components/Questions';
import QuestionsContextProvider from "./context/QuestionsContext.jsx"
import Result from './components/Result.jsx';
import SelectClass from "./components/SelectClassForQuestion.jsx"

function App() {
  return (
    <Router>
      <div className="App">
        <QuestionsContextProvider>
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/cbt/:id" component={Questions} />
            <Route path="/result" exact component={Result} />
            <Route path="/cbt" exact component={SelectClass} />
          </Switch>
        </QuestionsContextProvider>
      </div>
    </Router>
  );
}

function Home() {
  return (
    <div>
      <Navigation />
      <Slideshow />
      <About />
      <Testimony />
      <Footer />
    </div>
  )
}

export default App;
