import React from "react";
import Boy from "./boy.jpg";
import Avatar from "./avatar.jpg";
import Success from "./success.jpg";
import "uikit/dist/css/uikit.min.css";
import "uikit/dist/js/uikit.min.js";
import "uikit/dist/js/uikit-icons.js";



function Slideshow() {
    return (
        <div className="uk-slideshow-container uk-dark" uk-slideshow="autoplay:true">
            <div className="uk-position-relative" tabIndex="-1">
                <ul className="uk-slideshow-items">
                    <li><img src={Boy} alt="" /></li>
                    <li><img src={Success} alt="" /></li>
                    <li><img src={Avatar} alt="" /></li>
                </ul>

                {/* eslint-disable-next-line */}
                <a className="uk-position-center-left uk-position-small uk-light" href="#" uk-slidenav-previous="true" uk-slideshow-item="previous"></a>
                {/* eslint-disable-next-line */}
                <a className="uk-position-center-right uk-position-small uk-light" href="#" uk-slidenav-next="true" uk-slideshow-item="next"></a>


            </div><ul className="uk-slideshow-nav uk-dotnav uk-flex-center uk-margin"></ul>
        </div>
    );
}

export default Slideshow;