import React, { Component } from 'react';
import Success from "./success.jpg";
import Avatar from "./avatar.jpg";
import Boy from "./boy.jpg";
import "uikit/dist/js/uikit-icons.js";
import "uikit/dist/js/uikit.js";
import "uikit/dist/css/uikit.min.css";


class Testimony extends Component {
    constructor(props) {
        super(props);
        this.state = {
            testimonies: [
                {
                    image: Boy,
                    description: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Totam at, consectetur natus incidunt quisquam nesciunt voluptatem autem? Quam, optio quia!",
                    name: "Mr John Doe",
                    id: 1
                },
                {
                    image: Success,
                    description: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Totam at, consectetur natus incidunt quisquam nesciunt voluptatem autem? Quam, optio quia!",
                    name: "Mrs Jane Doe",
                    id: 2
                },
                {
                    image: Avatar,
                    description: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Totam at, consectetur natus incidunt quisquam nesciunt voluptatem autem? Quam, optio quia!",
                    name: "Mr Lorem Ipsum",
                    id: 3
                },
                {
                    image: Success,
                    description: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Id minus aliquid quod, architecto consequatur numquam sapiente consectetur optio praesentium, assumenda, alias cumque reiciendis voluptas vitae! Asperiores labore dolor harum dolorem.",
                    name: "Mr Fred Johnes",
                    id: 4
                }
            ]
        };
    }
    render() {
        return (
            <React.Fragment>
                <h2 className="uk-text-center">Testimonies</h2>
                <div className="uk-slider-container " data-uk-slider="center: true; autoplay:true; autoplay-interval:5000; pause-on-hover:true">
                    <div className="uk-position-relative uk-visible-toggle uk-light" tabIndex="-1">
                        <ul className=" uk-slider-items" data-uk-grid data-uk-height-match="target: > li > .uk-panel > .uk-card">
                            {this.state.testimonies.map(x => {
                                return (
                                    <li className="uk-width-3-4 uk-width-1-2@s" key={x.id} >
                                        <div className="uk-panel">
                                            <div className="uk-card uk-card-primary uk-grid-collapse  uk-margin" data-uk-grid>
                                                <div className="uk-card-media-top uk-cover-container">
                                                    <img src={x.image} alt={x.image} data-uk-cover />
                                                    <canvas width={x.width} height={x.height}></canvas>
                                                </div>
                                                <div className="uk-card-body">
                                                    <h3 className="uk-card-title">{x.name}</h3>
                                                    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Totam at, consectetur natus incidunt quisquam nesciunt voluptatem autem? Quam, optio quia!</p>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>

                    <ul className="uk-slider-nav uk-dotnav uk-flex-center uk-margin-small-top"></ul>
                </div>
            </React.Fragment >
        );
    }

}

export default Testimony;