import React from 'react';
import "uikit/dist/css/uikit.min.css";
import "uikit/dist/js/uikit.min.js";
import "uikit/dist/js/uikit-icons.js";
import { Link } from "react-router-dom";
import { QuestionsContext } from "../context/QuestionsContext.jsx"

class Questions extends React.Component {
    static contextType = QuestionsContext;
    render() {
        return (
            <div style={{ minHeight: "100vh" }}>
                <Form />
            </div>
        );
    }

}

class Form extends React.Component {
    static contextType = QuestionsContext;
    render() {
        const { questions, active, changeHandler, correctCounter, activeHandler } = this.context;
        const { question, option1, option2, option3, option4, id, option1Checked, option2Checked, option3Checked, option4Checked } = active;
        return (
            <>
                <h1 className="uk-flex uk-flex-center uk-padding-small">Question {id + 1}</h1>
                <Link to="/result"><button className="uk-button-small uk-button-default uk-position-top-right">Quit</button></Link>
                <form className="uk-margin-small-left uk-margin-small-right" >

                    <h4>{question}</h4>
                    <input className="uk-margin-small" type="radio" name="options" id={option1} value={option1} onChange={(e) => changeHandler(e, id)} checked={option1Checked} />{option1}<br />
                    <input className="uk-margin-small" type="radio" name="options" id={option2} value={option2} onChange={(e) => changeHandler(e, id)} checked={option2Checked} />{option2}<br />
                    <input className="uk-margin-small" type="radio" name="options" id={option3} value={option3} onChange={(e) => changeHandler(e, id)} checked={option3Checked} />{option3}<br />
                    <input className="uk-margin-small" type="radio" name="options" id={option4} value={option4} onChange={(e) => changeHandler(e, id)} checked={option4Checked} /><span>{option4}</span><br />

                    <div className="uk-flex-center uk-position-bottom uk-margin-medium-bottom">

                        <div className="uk-child-width-1-5 uk-position-bottom uk-margin-xlarge-bottom">
                            {questions.map(question => (
                                <Buttons quest={question} key={Math.random()} onClick={() => activeHandler(question)} color={question.color} />
                            ))}
                        </div>
                        <Link to="/result">
                            < input type="submit" value="Submit" className="uk-button uk-button-primary uk-width-1-1 uk-margin-small-bottom uk-margin-small-top " onClick={correctCounter} />
                        </Link>
                    </div>

                </form >
            </>
        );
    }
}

function Buttons(props) {
    //const { color } = props.color

    return (
        <button className="uk-button uk-button-default" onClick={props.onClick} style={{ backgroundColor: `${props.color}` }}>
            {props.quest.id + 1}
        </button>
    )
}
export default Questions;