import React from 'react';
import "uikit/dist/css/uikit.min.css";
import "uikit/dist/js/uikit.min.js";
import "uikit/dist/js/uikit-icons.js";
import { QuestionsContext } from "../context/QuestionsContext.jsx"
import { Link } from "react-router-dom";

class Result extends React.Component {
    static contextType = QuestionsContext;
    render() {
        const { cleanState } = this.context
        const { correct } = this.context;
        let bgColor = "";
        let performance = "";
        if (correct < 3) bgColor = "red";
        if (correct > 3 && correct < 6) bgColor = "blue";
        if (correct > 6) bgColor = "green";

        if (correct < 3) performance = " poor but try harder next time";
        else if (correct > 3 && correct < 6) performance = " average put in more effort";
        else if (correct > 6) performance = " exceptional keep it up";



        return (
            <div className="uk-light" style={{ minHeight: "100vh", backgroundColor: bgColor }}>

                <p style={{ display: "block", margin: "auto", paddingTop: "30vh", textAlign: "center" }} className="uk-text-large">Congratulations, you have completed your exam and your score is {correct}  you performance is {performance}
                </p>

                <div className=" uk-position-bottom uk-margin-large-bottom">
                    <Link to="/"><button className="uk-button uk-button-default uk-width-expand">Homepage</button></Link>
                    <Link to="/cbt"><button className="uk-button uk-button-default uk-margin-small-top  uk-width-expand" onClick={cleanState}>Take question again</button></Link>
                </div>
            </div >
        );
    }
}

//for those button create an onclick to clean up the state

export default Result;