import React, { Component } from 'react';
import Home from "uikit/src/images/icons/home.svg";
import Phone from "uikit/src/images/icons/phone.svg";
import Image from "uikit/src/images/icons/image.svg";
import Signin from "uikit/src/images/icons/sign-in.svg";
import User from "uikit/src/images/icons/user.svg";
import Question from "uikit/src/images/icons/question.svg";
import Laptop from "uikit/src/images/icons/laptop.svg";
import { Link } from "react-router-dom";

import UIkit from "uikit/dist/js/uikit.min.js";
import "uikit/dist/js/uikit-icons.min.js";
import "uikit/dist/css/uikit.min.css";
class Navigation extends Component {
    constructor() {
        super();
        this.state = {
            links: [{
                name: "Home",
                link: "/",
                active: true,
                logo: Home

            }, {
                name: "Contact Us",
                link: "#",
                active: false,
                logo: Phone
            }, {
                name: "Gallery",
                link: "#",
                active: false,
                logo: Image
            }, {
                name: "C B T",
                link: "/cbt",
                active: false,
                logo: Laptop
            }, {
                name: "Sign in",
                link: "#",
                active: false,
                logo: Signin
            }]
        };
    }

    render() {

        return (
            <nav className="data-uk-navbar-component data-navbar-transparent uk-background-muted uk-dark" data-uk-navbar>

                <div className="uk-navbar-left">
                    {/* eslint-disable-next-line */}
                    <a className="uk-navbar-item uk-logo" href="#.com">Ebenezer Bells Academy</a>
                </div>
                <div className="uk-navbar-right">
                    {/* eslint-disable-next-line */}
                    <a className="uk-navbar-toggle uk-button" data-uk-navbar-toggle-icon href="#" type="button" uk-toggle="target: #offcanvas-navigation"></a>
                </div>

                <div id="offcanvas-navigation" uk-offcanvas="overlay: true; mode:push" className="">
                    <div className="uk-offcanvas-bar uk-background-primary">
                        <button className="uk-offcanvas-close" type="button" data-uk-close></button>
                        <img className="uk-border-circle uk-margin-large-top uk-margin-small-left" width="200" height="200" src={User} alt="user"></img>
                        <ul className="uk-nav uk-nav-default uk-text-medium">
                            {
                                this.state.links.map(link => (
                                    <Link to={link.link} key={link.name}>
                                        <li className="uk-margin-small-top uk-active" onClick={() => {
                                            if (link.name === "Sign in") {
                                                UIkit.notification({
                                                    message: "This feature is currently unavaliable",
                                                    status: "danger",
                                                    timeout: 1000
                                                });
                                            }
                                        }}><img src={link.logo} alt={link.name} className="uk-margin-small-right" />{link.name}</li>
                                    </Link>
                                ))
                            }
                            <ul className="uk-margin-large-top" data-uk-accordion>
                                <li className="uk-light" key={"unique"}>
                                    {/* eslint-disable-next-line */}
                                    <a className="uk-accordion-title"><img src={Question} alt={Question} className="uk-margin-small-right" />Inquiry</a>
                                    <div className="uk-accordion-content"><p>Lorem ipsum dolor sit amet consectetur adipisicing elit.</p></div>
                                </li>
                            </ul>
                        </ul>

                    </div>
                </div>
            </nav>
        );
    }
}

export default Navigation;