import React from "react";
import Success from "./success.jpg";
import Avatar from "./avatar.jpg";
import Boy from "./boy.jpg";
// import UIkit from "uikit";

import "uikit/dist/js/uikit-icons.js";
import "uikit/dist/js/uikit.js";
import "uikit/dist/css/uikit.min.css";

class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            about: [
                {
                    image: Boy,
                    width: 4896,
                    height: 3264,
                    description: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Totam at, consectetur natus incidunt quisquam nesciunt voluptatem autem? Quam, optio quia!",
                    heading: "Quality Education",
                    id: 1
                },
                {
                    image: Success,
                    width: 5472,
                    height: 3072,
                    description: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Totam at, consectetur natus incidunt quisquam nesciunt voluptatem autem? Quam, optio quia!",
                    heading: "Easy payment",
                    id: 2
                },
                {
                    image: Avatar,
                    width: 800,
                    height: 800,
                    description: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Totam at, consectetur natus incidunt quisquam nesciunt voluptatem autem? Quam, optio quia!",
                    heading: "Sound teachers",
                    id: 3
                },
                {
                    image: Success,
                    width: 5472,
                    height: 3072,
                    description: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Id minus aliquid quod, architecto consequatur numquam sapiente consectetur optio praesentium, assumenda, alias cumque reiciendis voluptas vitae! Asperiores labore dolor harum dolorem.",
                    heading: "Jesus is Lord",
                    id: 4
                }
            ]
        };
    }

    render() {
        return (
            <div>
                <h1 className="uk-flex uk-flex-center">About<span uk-icon="cog"></span></h1>
                {this.state.about.map(x => (
                    <div key={x.id} className="uk-card uk-card-primary uk-card-hover uk-grid-collapse uk-child-width-1-2@s uk-margin uk-margin-small-left uk-margin-small-right" data-uk-grid>
                        <div className="uk-card-media-left uk-cover-container">
                            <img src={x.image} alt="boy" data-uk-cover />
                            <canvas width={x.width} height={x.height}></canvas>
                        </div>
                        <div className="uk-card-body">
                            <h3 className="uk-card-title">
                                {x.heading}
                            </h3>
                            <p>{x.description}</p>
                        </div>
                    </div>
                ))}
            </div>
        );
    }
}

export default About;